.home {
  padding-top: 30px;
  padding-bottom: 25px;
}

.home__box {
  position: relative;
  display: block;
}

.home__title {
  margin: 0;
  text-align: center;
  position: absolute;
  left: 0;
  right: 0;
  top: 68px;
  color: #fff;
  text-transform: uppercase;
}

.blog__img {
  background-size: cover;
  background-position: center center;
  width: 260px;
  height: 250px;
  position: relative;
}

.blog__date {
  position: absolute;
  top: 15px;
  left: 0;
  background-color: #fff;
  padding: 8px 11px;
  font-size: 14px;
  color: #000;
}

.blog__box {
  display: block;
}

.blog__box:hover, .blog__box:focus, .blog__box:link {
  text-decoration: none;
  color: #9d9d9d;
}

.blog__text {
  color: #9d9d9d;
  font-size: 18px;
  height: 80px;
  overflow: hidden;
  margin-bottom: 45px;
}

.blog__single-date {
  color: #000;
  font-size: 14px;
}

.blog__single-img {
  height: 309px;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  margin-bottom: 45px;
}

.catalog__header {
  border-bottom: 1px solid #bababa;
  margin-bottom: 23px;
}

.catalog__category {
  color: #000;
  font-size: 24px;
  text-transform: uppercase;
  display: inline-block;
  margin-right: 68px;
}

.catalog__category:hover {
  color: #000;
}

.catalog__category:last-child {
  margin-right: 0px;
}

.catalog__button {
  padding-top: 20px;
  padding-bottom: 20px;
  width: 100%;
  background-color: #000;
}

.catalog__menu {
  color: #9d9d9d;
  transition: all .4s;
  padding-right: 40px;
  font-size: 24px;
}

.catalog__menu:hover, .catalog__menu:link, .catalog__menu:focus {
  text-decoration: none;
  color: #000;
}

.catalog__menu_active {
  color: #000;
}

.catalog__main {
  overflow: hidden;
  padding: 0 21px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 381px;
}

.sp__wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 475px;
}

.sp__img {
  max-width: 100%;
  max-height: 100%;
  width: auto;
  height: auto;
  display: block;
}

.sp__carousel {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 110px;
}

.sp__button {
  margin-top: 45px;
}

.sp__list {
  list-style-type: none;
  padding-left: 0;
}

.sp__list_padding {
  padding-top: 44px;
}

.sp__list-title {
  padding-top: 54px;
}

.sp__item {
  padding-top: 20px;
}

.sp__item:first-child {
  padding-top: 0;
}

.sp__text {
  padding-top: 90px;
}

.sp__recomendation {
  padding-top: 60px;
  padding-bottom: 18px;
  font-size: 36px;
  color: #000;
  margin: 0;
}

.ordering {
  padding-top: 25px;
}

.ordering__main {
  padding-left: 40px;
  padding-right: 30px;
}

.ordering__title {
  font-size: 36px;
  color: #000;
  margin-bottom: 0;
  margin-top: 0;
}

.ordering__title_main {
  padding-top: 35px;
}

.ordering__subtitle {
  margin-bottom: 0;
  padding-top: 25px;
  font-size: 14px;
  text-transform: uppercase;
  color: #000;
}

.ordering__subtitle_p0 {
  padding: 0;
  margin-top: 0;
}

.ordering__position {
  display: flex;
  justify-content: space-between;
  margin-top: 29px;
}

.ordering__position_center {
  align-items: center;
}

.ordering__button {
  margin-top: 45px;
  margin-bottom: 30px;
}

.ordering__good {
  display: flex;
  justify-content: space-between;
}

.ordering__item {
  padding-top: 25px;
  padding-bottom: 38px;
  margin-top: 35px;
}

.ordering__item-header {
  border-bottom: 1px solid #fff;
  padding-bottom: 22px;
  padding-left: 13px;
  padding-right: 13px;
}

.ordering__item-body {
  padding-top: 22px;
}

.form__name, .form__radio-text {
  font-size: 14px;
  color: #000;
  margin: 0;
}

.form__name_textarea {
  margin-top: 14px;
}

.form__field {
  border: 1px solid #e8e8e8;
  text-indent: 15px;
  height: 42px;
  color: #000;
  text-transform: capitalize;
}

.form__field_main {
  max-width: 459px;
  width: 100%;
}

.form__field_textarea {
  min-height: 110px;
  max-width: 459px;
  width: 100%;
  padding-top: 10px;
  padding-left: 10px;
  padding-bottom: 10px;
  resize: vertical;
}

.form__field::placeholder {
  color: #000;
}

.form_black {
  color: black;
}

.form__radio {
  display: flex;
  align-items: center;
  position: relative;
  margin-top: 18px;
}

.form__radio-inner {
  position: absolute;
  visibility: hidden;
}

.form__radio-btn {
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 1px solid #000;
  top: -1px;
}

.form__radio-btn::before {
  position: absolute;
  content: '';
  display: block;
  border-radius: 50%;
  width: 12px;
  height: 12px;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  transition: all .3s;
}

.form__radio-text {
  z-index: 9;
  position: relative;
  cursor: pointer;
  padding-left: 35px;
}

.form__radio-inner:checked ~ div.form__radio-btn:before {
  background: #000;
}

.cart__pay {
  padding-bottom: 40px;
  padding-top: 33px;
}

.cart__item {
  padding-top: 35px;
  padding-bottom: 20px;
  padding-left: 15px;
  padding-right: 15px;
}

.cart__table {
  width: 100%;
}

.cart__table-row {
  border-bottom: 1px solid #f6f6f6;
}

.cart__table-title {
  color: #1e1e1e;
  padding-bottom: 15px;
}

.cart__table-data {
  padding: 30px 0;
}

.cart__table-thumbnail {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 42px;
  width: 42px;
  margin-right: 18px;
}

.cart__table-img {
  max-width: 100%;
  max-height: 100%;
  height: auto;
  width: auto;
}

.cart__table-main {
  display: flex;
}

.cart__table-main:hover, .cart__table-main:link, .cart__table-main:focus {
  text-decoration: none;
}

.cart__table-result {
  height: 40px;
  border: 1px solid #e8e8e8;
  max-width: 59px;
  text-align: center;
  color: #1e1e1e;
  margin: 0 12px;
}

.cart__table-plus, .cart__table-minus {
  cursor: pointer;
}

.cart__table-plus {
  transform: rotate(180deg);
}

.cart__table-close {
  color: #ededed;
  font-size: 35px;
  cursor: pointer;
}

@font-face {
  font-family: 'FuturaDemic';
  src: url(../fonts/FuturaDemiC.ttf);
}

.nav_footer {
  padding-top: 45px;
  padding-bottom: 50px;
}

.nav__list {
  padding-left: 0px;
  list-style-type: none;
}

.nav__list-child {
  display: none;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.6);
  left: 0;
  top: 52px;
  z-index: 1;
  padding-top: 20px;
  padding-bottom: 20px;
  right: 0;
  line-height: 1.5;
}

.nav__item_inline {
  display: inline-block;
}

.nav__item_has-child {
  position: relative;
  cursor: pointer;
}

.nav__item_has-child:hover > .nav__list-child {
  display: block;
}

.nav__item:last-child .breadcumbs__link::after {
  display: none;
}

.nav__arrow {
  position: absolute;
  right: 0px;
  top: 2px;
  color: #000;
  font-size: 9px;
}

.nav__link {
  position: relative;
  font-weight: 400;
  transition: all .5s;
}

.nav__link_dark {
  color: #000;
}

.nav__link_light {
  color: #fff;
}

.nav__link_uppercase {
  text-transform: uppercase;
}

.nav__link_capitalize {
  text-transform: capitalize;
}

.header {
  line-height: 85px;
  border-bottom: 4px solid #cdcdcd;
}

.header__logo {
  position: relative;
}

.header__line {
  top: 45px;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  background: white;
}

.header__link {
  font-size: 20px;
  padding-left: 10px;
  display: block;
  padding-right: 10px;
  transition: all .3s;
}

.header__link_child {
  padding: 0;
}

.header__link:hover:before, .header__link:hover:after {
  width: 50%;
}

.header__link:before {
  position: absolute;
  content: '';
  width: 0;
  height: 2px;
  bottom: 28px;
  transition: all .3s;
  left: 0;
  background-color: #000;
}

.header__link:after {
  position: absolute;
  content: '';
  width: 0;
  bottom: 28px;
  height: 2px;
  right: 0;
  transition: all .5s;
  background-color: #000;
}

.nav__item:first-child .header__link__link {
  padding-left: 0;
}

.nav__item:last-child .header__link__link {
  padding-right: 0;
}

.header__link:hover, .header__link:focus, .header__link:link {
  color: #000;
  text-decoration: none;
}

.footer {
